import React from 'react';

const asyncStateHoc = WrappedComponent => {
	// 
	// console.log('asyncStateHoc inject...');
	return class extends WrappedComponent {
		/*constructor(props) {
			super(props);
			console.log(this);
		}*/
		setStateAsync = (state) => {
			// state
			return new Promise((resolve, reject)=>{
				const res = true;
				if (res) {
					this.setState(state, resolve(true));
				} else {
					reject(false);
				}
			});
		};
		textover = (contents='', Maximum_length_allowed=8) => {
			const isOver = contents.length > Maximum_length_allowed;
			if (isOver) {
				return contents.slice(0, Maximum_length_allowed) + '...';
			}
			return contents;
		};
		setState_extendOBJ = (stateName, key, value) => {
			// state
			// const isObject = window.jQuery.isPlainObject(stateName); // 
			let _data = {...this.state[stateName]};
			console.log(_data);
			if (_data) {
				_data[key] = value;
				this.setState({
					[stateName]: _data
				});
			} else {
				throw new Error('');
			}
		};
		render() {
			return super.render();
		}
	};
};

const proxyPropertyHoc = WrappedComponent => {
	// 
	return class extends React.Component {
		constructor(props) {
			//  / 
			// superthissuper
			super(props);
			this.name = 'propertyProxy';
		}
		getTime = () => {
			return new Date().getTime();
		};
		render() {
			const props = {
				...this.props,
				getTime: this.getTime
			};
			return <WrappedComponent {...props} />;
		}
	};
};

const hijackRenderHoc = config => WrappedComponent => class extends WrappedComponent {
	// 
	render() {
		const { style = {} } = config;
		//  / 
		const elementsTree = super.render();
		console.log(elementsTree, 'elementsTree');
		if (config.type === 'add-style') {
			return <div style={{...style}}>
				{elementsTree}
			</div>;
		}
		return elementsTree;
	}
};

export {
	asyncStateHoc,
	proxyPropertyHoc,
	hijackRenderHoc
};