/**/

/*import React Base*/
import React from 'react';
import ReactDOM from 'react-dom';

/*import global stylesheet*/
import './index.css';
import './style.less';

/*import router*/
import Router from './router';

/*import global config for axios*/
import './config';

/*import serviceWorker for production*/
import * as serviceWorker from './serviceWorker';

/*import empty components*/

import Empty from './containers/Empty';

// 
if (window.navigator.onLine) {
	ReactDOM.render(<Router />, document.getElementById('rexhang-wrap'));
} else {
	ReactDOM.render(<div style={{marginTop: 200}}><Empty title='' src={require('./img/notonline.svg')} /></div>, document.getElementById('rexhang-wrap'));
}

/*
 * https: service worker
 * registerServiceWorkerreactservice worker
 * 
 * registerServiceWorkerservice worker process.env.NODE_ENV === 'production'
 */
// if(process.env.NODE_ENV === 'production'){
// 	serviceWorker.register();
// } else {
// 	serviceWorker.unregister();
// }
serviceWorker.unregister();

